<template>
<br-transition name="horizontalBlurRight">
    <section v-if="show" ref="notify" class="br-notify group" :class="`br-notify__${level}`" @mouseover="hovered=true" @mouseleave="hovered=false">
        <article>
            <div v-if="autoClose" ref="progress" class="br-notify--progress"></div>
            <slot name="preicon">
                <section v-if="level" class="br-notify-preicon">
                    <i-gg-info v-if="level == 'info'" />
                    <i-gg-close-o v-if="level == 'danger'" />
                    <i-gg-danger v-if="level == 'warning'" />
                    <i-gg-check-o v-if="level == 'success'" />
                </section>
            </slot>
            <div class="br-notify--content">
                <span>
                    <slot></slot>
                </span>
                <span v-if="$slots.desc">
                     <slot name="desc"></slot>
                </span>
                <span v-if="$slots['inline-actions']">
                     <slot name="inline-actions"></slot>
                </span>
            </div>
            <slot name="aftericon">
                <section v-if="closable && !$slots.actions" >
                    <i-eva-close-fill v-if="closable" class="br-notify--close" @click="emits('update:show', false)" />
                </section>
            </slot>
            <slot name="actions"></slot>
        </article>
    </section>
</br-transition>
</template>

<script setup>
import { toRefs, watch, useSlots, nextTick, ref, watchEffect, onMounted } from "vue"
import {gsap} from 'gsap'

const hovered = ref(false)
const notify = ref(null)
const progress = ref(null)
let timeline = gsap.timeline()
const emits = defineEmits(['update:show'])
const slots = useSlots()
const props = defineProps({
    autoClose: {
        default: false,
        type: Boolean
    },
    closable: {
        default: true,
        type: Boolean,
    },
    level: {
        default: '',  // or info, danger, warning, success
        type: String
    },
    onComplete: {
        default: () => {},
        type: Function
    },
    show: {
        default: false,
        type: Boolean
    }
})

const {show, autoClose, onComplete} = toRefs(props)

function retrieveNotificationLen (slot) {
    if (!slot.el && slot.children) return slot.children.length * 60 / 1000
    if (!slot.el && slot.dynamicProps[0]) return slot.props.innerHTML.length * 60 / 1000
    const el = slot.el
    const chars = el.textContent
    const caps = chars.match(/[A-Z]/g)
    const charCount = chars.length + (caps ? caps.length - 1 : 0)
    return charCount * 60 / 1000
}

watch(show, async flag => {
    if (timeline && timeline.isActive()) return
    
    await nextTick()
    const defSlot = slots.default()[0]
    const descSlot = slots.desc && slots.desc()[0]

    if (defSlot && flag && autoClose.value) {
        // the main slot first
        const main = retrieveNotificationLen(defSlot)
        const desc = descSlot && retrieveNotificationLen(descSlot) || 0
        timeline.to(progress.value, {
            width: notify.value.offsetWidth,
            delay: 1,
            duration:  main + desc,
            ease: 'linear',
            onComplete: () => {
                emits('update:show', false)
                timeline = gsap.timeline()
                if (onComplete.value) onComplete.value()
            }
        })
    }
}, {immediate: true})

watchEffect(async () => {
    if (hovered.value) {
        timeline.pause()
    }
    else if (!hovered.value && timeline && timeline.paused()) {
        setTimeout(() => {
            timeline.resume()
        }, 200)
    }
})
</script>

