    @apply fixed top-0 right-0 m-8 z-1000 min-w-xs max-w-md select-none block ;
}
.br-notify--progress {
    @apply absolute transition duration-400 transform group-hover:(opacity-70 -translate-y-0.2) top-0 left-0 h-0.75 bg-$icon opacity-80;
}

