    --icon: var(--br-success-text);
}
.br-notify__warning {
    --icon: var(--br-warning-text);
}
.br-notify__danger {
    --icon: var(--br-danger-text);
}

.br-notify > article {
    @apply flex relative overflow-hidden w-full bg-white shadow-lg py-3.5 px-3 rounded-lg border border-$br-border-color;
}

.br-notify-preicon {
    @apply text-$icon self-start mx-1;
}

.br-notify-preicon > svg{
    @apply h-5 w-5;
}

.br-notify--content {
    @apply ml-2 flex-col flex flex-grow space-y-1 self-center pr-2;
    font-size: .96.5rem;
    line-height: 19px;
}

.br-notify--content > span:nth-child(1) {
    @apply font-medium;
}

.br-notify--content > span:nth-child(2) {
    @apply text-dark-500 text-xs;
    font-size: 15.5px;
}

.br-notify--close {
    @apply text-gray-400 !w-6 !h-6 cursor-pointer transition transform hover:(text-gray-400/90 scale-90) active:(text-gray-500 rotate-45);
}
</style>