<template>
<!-- TODO: implement hidden flag for each field -->
<form class="br-form" :class="`br-form__${formType}`" novalidate target="_self" enctype="application/json" @submit.prevent="onSubmit">
   
        <teleport to='body'>
            <br-notification v-model:show="notif.open" :auto-close="true" :level="notif.level">
                <template #default>
                    <span v-html="notif.msg"> </span>
                </template>
                <template v-if="notif.desc" #desc>
                    {{ notif.desc }}
                </template>
                <!-- <template #inline-actions>
                    <section class="flex space-x-6 mt-3">
                        <span @click="notif.open=false" class="br-link">Undo</span>
                        <span @click="notif.open=false" class="br-link br-primary">Dismiss</span>
                    </section>
                </template> -->
            </br-notification>
        </teleport>

        <section>
        
            <slot name="before-fields" :errors="errors"/>

            <section class="br-form--main" :class="wrapperClass">
                <template v-for="(sectionObj, sectionName) in fields" :key="sectionName">
                    <br-transition name="horizontalBlur2">
                        <article v-if="!sectionObj.hidden" :ref="el => {if(el) sectionRefs[sectionName] = el}" class='br-form--section' :class="sectionObj.classes || []">
                            <slot v-if="sectionName" :name="`before-section-${sectionName}`" />
                            <slot v-if="sectionName" :name="`section-${sectionName}`" v-bind="sectionObj">
                                <header v-if="sectionObj.label" class="br-form--section-header">
                                    <h3 v-if="sectionObj.label">{{ sectionObj.label }}</h3>
                                    <div v-if="sectionObj.helpText">{{ sectionObj.helpText }}</div>
                                </header>
                            </slot>
                            <slot v-if="sectionName" :name="`prepend-section-${sectionName}`" />

                            <br-transition name="dissolve" class="br-form--fields-wrap" tag="div" type="group">
                                <template v-for="fieldObj in sectionObj.fields" :key="fieldObj.name">
                                    <div :id="fieldObj.id" :ref="el => {if(el) fieldRefs[fieldObj.name] = el}"
                                        class="field"
                                        tabindex="0"
                                        :class="[
                                            rootErrors[fieldObj.name] ? 'br-errored' : '',
                                            fieldObj.parentClasses || ''
                                        ]"
                                        @click="onFieldSelect"
                                        @focus="onFieldSelect">

                                        <article v-if="fieldObj.displayExtLabel" class="br-form--ext-label">
                                            <div v-if="fieldObj.isRequired" class="br-form--ext-label--required"></div>
                                            <label>{{ fieldObj.label }}</label>
                                            <br-input-loading :loading="fieldObj.loading"/>
                                        </article>

                                        <div v-else-if="!fieldObj.isCompound && fieldObj.isRequired" class="br-form--ext-label--required br-plain-field" :class="focusedFields[fieldObj.name] ? 'br-plain-field__focused' : ''">
                                            <span></span>
                                        </div>
                                        
                                        <section>
                                            <div class="br-form--field">
                                                <component
                                                    :is="useSelectComponent(fieldObj.type)"
                                                    v-model="values[fieldObj.name]"
                                                    v-bind="fieldObj"
                                                    :class="[ fieldObj.classes || '' ]"
                                                    @update:auxModel="handleAuxModelUpdate"
                                                    @fieldupdated="fieldObj.handleFieldDebounced"
                                                    @field-blur="({valid}) => fieldObj.handleFieldBlur(valid, fieldObj.name, values[fieldObj.name])"
                                                    @focus-toggle="focusedFields[fieldObj.name]=true"
                                                >
                                                    <!-- This trick passes all of the underlying slots to the current context -->
                                                    <template v-for="(_, slotName) in $slots" #[slotName]="slotData">
                                                        <slot :name="slotName" v-bind="slotData" />
                                                    </template>
                                                </component>
                                            </div>
                                        </section>

                                        <slot :name="`after-field-${fieldObj.name}`" />

                                    </div>
                                </template>

                                <slot name="after-fields" />

                            </br-transition>
                        </article>
                    </br-transition>
                </template>
            </section>
            <div class="br-form--submit">
                <slot name="submit-button" v-bind="{formMode, meta, isSubmitting, submitText, submittingText}">
                    <br-button v-if="formMode === 'perForm'" class="br-btn--main br-form--submit-btn" 
                        type="submit" :disabled="meta.touched && !meta.valid" :loading="isSubmitting">
                            <template v-if="!isSubmitting">
                                {{ submitText }}
                            </template>
                            <template v-else>
                                {{ submittingText }}
                            </template>
                    </br-button>
                </slot>
            </div>
    </section>
</form>
</template>

<script setup>
// TODO: If meta.initialValues contains currently iterated field and that field has just produced the 'updated' event,
// we shall perform the API request right away through `this.submitForm`
const PERSIST_EXCLUDED_FIELDS = ['password']

import { toRefs, computed, provide, reactive, watch, ref, getCurrentInstance} from 'vue'
import { useForm, FormContextKey } from 'vee-validate'
import { useDebounceFn } from '@vueuse/shared'
import { getAndParseLS } from '../utils'
import { useSelectComponent, blurryFieldTypes } from './composables'
import {object} from 'yup'

const emit = defineEmits(['update:modelValue'])
const props = defineProps({
    name: {
        type: String,
        required: true
    },
    debounceInterval: {
        type: Number,
        default: 700
    },
    modelValue: {
        type: Object,
        default: () => ({})
    },
    onFieldValid: {
        type: Function,
        default: async () => {}
    },
    onFormErrors: {
        type: Function,
        default: async () => {}
    },
    onFormValid: {
        type: Function,
        default: async () => {}
    },
    persistState: {
        // only in perForm mode before submitting
        type: Boolean,
        default: false
    },
    rootLoading: {
        type: Boolean,
        default: false
    },
    submitText: {
        type: String,
        default: 'Submit'
    },
    submittingText: {
        type: String,
        default: 'Submitting...'
    },
    schema: {
        type: Object,
        default: () => ({})
    },
    wrapperClass: {
        type: [Array, String],
        default: ''
    }
})

const focusedFields = reactive({})
const { modelValue, schema, debounceInterval, name, persistState, rootLoading } = toRefs(props)
const multiSelectionTypes = ['checkbox', 'switch']
const auxModelMap = reactive({})
const fieldRefs = ref({})
const sectionRefs = ref({})
const fieldLoading = reactive({})
const fieldSuccessText = ref({})
const isSubmittedValid = ref(false)
const FORM_PERSIST_KEY = `br:formperm:${name.value}`
const notif = reactive({
    msg: '',
    desc: '',
    open: false,
    level: 'info'
})

function notify (msg, desc, level) {
    notif.level = level
    notif.msg = msg
    notif.desc = desc
    notif.open = true
}

function closeNotif () {
    notif.open = false
}

const formMode = computed(() => schema.value.form && schema.value.form.save || 'perForm')
const formType = computed(() => (schema.value.form || {}).type || "stacked")
const compoundFieldTypes = ['selectionBox', 'decision', 'mapping']
const fieldIsCompoundMap = computed(() => Object.fromEntries(schema.value.fields.map(i => ([i.name, compoundFieldTypes.includes(useSelectComponent(i.type, true))]))))
const persistExcludedFields = computed(() => schema.value.fields.filter(field => PERSIST_EXCLUDED_FIELDS.includes(field.type)).map(field => field.name))

const persistFormValues = val => {
    if (formMode.value === 'perForm' && persistState.value) {
        const persistableValues = Object.fromEntries(Object.entries(val).filter(([key, value]) => value !== undefined && !persistExcludedFields.value.includes(key)))
        localStorage.setItem(FORM_PERSIST_KEY, JSON.stringify(persistableValues))
    }
}

const validationSchema = computed(() => {
    let localSchema = Object.fromEntries(
        schema.value.fields.map((item, idx) => {
            if (item.type === 'select') {
                // now it's time to infer if it's single or multiple choice select
                schema.value.fields[idx]['mode'] = item.rules.type === 'array' ? 'multi' : 'single'
            }
            else if (item.type === 'radio') {
                schema.value.fields[idx]['mode'] = 'single'
            }
            else if (multiSelectionTypes.includes(item.type)) {
                schema.value.fields[idx]['mode'] = 'multi'
            }
            else if (item.type === 'mapping') {
                const {data, rules: {fields}} = item
                for (const kk in data) {
                    const {label} = data[kk]
                    fields[kk].spec.label = label
                }
            }
            // each item *should* contain the 'label' key.
            // Use it to fill out the label function on yup rules.
            return [item.name, item.rules.label(item.label)]
        })
    )
    return object().shape(localSchema)
})

const calcModelValue = computed(() => {
    var base = reactive(modelValue.value || {})
    if (formMode.value === 'perForm' && persistState.value) {
        // perf lookup on localStorage for the latest copy of the current form
        let localCopy = getAndParseLS(FORM_PERSIST_KEY) || {}
        Object.assign(localCopy, base)
        base = localCopy
    }
    return base
})

const fieldsExemptFromSameValGuard = computed (() => {
    const exemptFieldTypes = ['mapping']
    return schema.value.fields.filter(({type}) => exemptFieldTypes.includes(type)).map(({name}) => name)
})

const { handleSubmit, isSubmitting, setFieldError, setErrors, errors, meta, submitForm, values } = useForm({
    validationSchema: validationSchema.value,
    initialValues: calcModelValue.value
})

const rootErrors = computed(() => {
    const ret = Object.entries(errors.value).reduce((acc, curr) => {
        let [key, val] = curr
        if (key.match('.')) {
            key = key.split('.')[0]
            if (!acc[key]) {
                acc[key] = []
            }
            acc[key].push(val)
        }
        else {
            acc[key] = val
        }
        return acc
    }, {})
    return ret
})

provide("IS_FORM_SUBMITTING", isSubmitting)
provide("FORM_MODE", formMode)
const app = getCurrentInstance()

const formTypesWithoutLabel = ['stacked-int', 'stacked-2col-int', 'stacked-disjoint-int']

const rawSchema = computed(() => {
    const describedSchema = Object.entries(validationSchema.value.resolve({}).describe().fields)
    return Object.fromEntries(describedSchema.map(([key, obj]) => { 
        return [key, obj.type === 'object' ? [obj] : obj.tests]
    }))
})

const isLoading = ({name}) => rootLoading.value || isSubmitting.value || !!fieldLoading[name]
const fieldsRecord = {}

const debounceFieldUpdate = useDebounceFn(async ([val, name]) => {
    let oldVal = fieldsRecord[name]
    oldVal = (oldVal == undefined || oldVal == null) ? meta.value.initialValues[name] : oldVal
    if (val instanceof Object) {
        if (JSON.stringify(val) === JSON.stringify(oldVal)) {
            return
        }
        fieldsRecord[name] = JSON.parse(JSON.stringify(val))
    }
    else {
        if (oldVal == val) return
        fieldsRecord[name] = val
    }

    fieldLoading[name] = true
    const setSuccessText = msg => fieldSuccessText.value[name] = msg
    const setError = msg => setErrors({[name]: msg})
    await props.onFieldValid(name, {value: val, setSuccessText, setError})
    fieldLoading[name] = false
}, debounceInterval.value)

const onBlurFieldUpdate = async (name, val, auxFieldName=null) => {
    if (!val && val !== 0 && val !== false) {
        return
    }
    let oldVal = fieldsRecord[auxFieldName || name]
    oldVal = (oldVal == undefined || oldVal == null) ? meta.value.initialValues[auxFieldName || name] : oldVal
    if (val instanceof Object) {
        if (JSON.stringify(val) === JSON.stringify(oldVal)) {
            return
        }
        fieldsRecord[auxFieldName || name] = JSON.parse(JSON.stringify(val))
    }
    else {
        if (oldVal == val) return
        fieldsRecord[auxFieldName || name] = val
    }

    fieldLoading[name] = true
    const setSuccessText = msg => {
        fieldSuccessText.value[name] = msg
    }
    const setError = msg => setErrors({[name]: msg})
    await props.onFieldValid(auxFieldName || name, {value: val, setSuccessText, setError})
    fieldLoading[name] = false
}

const handleAuxModelUpdate = async ([compoundName, auxModelValue]) => {
    const [origFieldName, _] = compoundName.split(":")
    const modelValueKey = auxModelMap[compoundName]
    calcModelValue.value[modelValueKey] = auxModelValue
    emit('update:modelValue', calcModelValue.value)
    if (formMode.value === 'perField') {
        await onBlurFieldUpdate(origFieldName, auxModelValue, modelValueKey)
    }
    else {
        persistFormValues(calcModelValue.value)
    }
}

const rawFields = computed(() => {
    if (!schema.value) return {}
    const fields = []

    for (let item of schema.value.fields) {
        const {models, name: fieldName, hidden} = item

        if (hidden) continue

        // set field compound
        const isFieldCompound = fieldIsCompoundMap.value[fieldName]
        item.isCompound = isFieldCompound

        if (formMode.value === 'perField') {
            // set field update handlers (debounce or onblur)
            
            if (!blurryFieldTypes.includes(item.type)) {
                item.handleFieldDebounced = debounceFieldUpdate
            }
            else {
                item.handleFieldBlur = async (valid, ...args) => {
                    focusedFields[fieldName] = false
                    if (valid) await onBlurFieldUpdate(...args)
                }
            }

            // set successText on a field
            item.successText = fieldSuccessText.value[fieldName]
            
            // set loading flag
            item.loading = isLoading({name: fieldName})
        }
        else {
            item.handleFieldDebounced = item.handleFieldBlur = () => {
                focusedFields[fieldName] = false
            }
        }

        // set schema
        item.schema = rawSchema.value[fieldName]

        // set pure yup validation schema
        item.validationSchema = validationSchema.value.fields[fieldName]

        // set required flag
        item.isRequired = Object.values(item.schema).some(({name}) => name === 'required')

        // set displayLabel flag
        if (formType.value.match('-int')) {
            item.displayExtLabel = false
            item.displayLabel = true
        }
        else {
            const needsLabelForm = !formTypesWithoutLabel.includes(formType.value)
            item.displayExtLabel = needsLabelForm && !isFieldCompound
            item.displayLabel = needsLabelForm && isFieldCompound
        }

        // set ID
        item.id = item.id != undefined ? item.id : `br-field--${item.name}`

        // handle aux models (vide br-input)
        if (models) {
            Object.entries(models).map(([modelKey, {name}]) => {
                // fix it!
                delete schema.value.models
                item[name] = calcModelValue.value[modelKey]
                auxModelMap[`${fieldName}:${name}`] = modelKey
            })
        }
        fields.push(item)
    }

    return {
        ...schema.value,
        fields
    }
})

const fields = computed(() => {
    let localSchema = Object.assign({}, rawFields.value)
    const sections = localSchema.sections
    if (!sections) return {'': {fields: localSchema.fields}}
    for (let section in localSchema.sections) {
        sections[section].fields = new Set()
    }
    for (let item of localSchema.fields) {
        const {section="default"} = item
        if (!sections[section]) sections[section] = {}
        if (!sections[section].fields) sections[section].fields = new Set()
        sections[section].fields.add(item)
    }
    return sections
})

const onFieldSelect = () => {
    // basically clear out all success messages
    fieldSuccessText.value = {}
}

const onInvalidSubmit = async ({ values, errors, results }) => {
    await props.onFormErrors({values, errors, results, notify})
}

const handleFormUpdate = handleSubmit(async values => {
    // clear the persistence cache for this form
    localStorage.setItem(FORM_PERSIST_KEY, JSON.stringify({}))
    await props.onFormValid(values, {setErrors, notify})
    isSubmittedValid.value = true

    const form = app.provides[FormContextKey]
}, onInvalidSubmit)


const onSubmit = (...args) => {
    if (formMode.value === 'perField') return
    return handleFormUpdate(...args)
}

watch(values, (val) => {
    emit('update:modelValue', val)
    if (!isSubmittedValid.value) {
        persistFormValues(val)
    }
})

</script>

